.mainContainer {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column; }

.bigContainer {
  border: 1px solid red;
  width: 460px;
  height: 40rem;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center; }

.track {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center; }

.shifting {
  transition: all 0.5s ease-in-out; }

.box {
  width: 150px;
  height: 150px;
  border: 1px solid black;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center; }
